    methods: {
        /**
         * 
         */
        initMoreData: function(key) {
            this.moreShows.videos = []; // 
            this.moreShows.name = this.filtedArray[key].name; // 
            this.moreShows.info = this.filtedArray[key].info; // 
            this.moreShows.pages = this.filtedArray[key].pages; // 
            for (let i = 0; i < this.filtedArray[key].pages.length; i++) {
                this.moreShows.videos.push(this.filtedArray[key].pages[i].video); // push
            }
            this.moreTemp = key; // 
            if (this.activeMore === false) this.activeMore = true; // 
        },
        /**
         *  
         * @param {Number} year 
         */
        yearFilter: function(year) {
            let mainBlock = document.querySelector(".main_block"); // 
            this.filtedArray = []; // 
            if (year) { // 
                this.yearTemp = year; // 
                for (let i = 0; i < this.portfolios.length; i++) {
                    if (this.portfolios[i].year === year) this.filtedArray.push(this.portfolios[i]); // i18n
                }
                for (let j = 0; j < this.sideOptions.length; j++) {
                    this.sideOptions[j].onActive = false; // 
                    if (this.sideOptions[j].name === year) this.sideOptions[j].onActive = true; // 
                }
                mainBlock.scrollTo(0, 0); // 
            } else if (!year) { // 
                this.filtedArray = this.portfolios; // i18n
            }
        },
        /**
         * 
         */
        countPortfolios: function() {
            let yearArray = [];
            for (let i = 0; i < this.sideOptions.length; i++) {
                yearArray.push(this.sideOptions[i].name); // yearArray
                for (let j = 0; j < this.filtedArray.length; j++) {
                    if (this.filtedArray[j].year === yearArray[i]) { 
                        this.sideOptions[i].count ++; // count+1
                    }
                }
            }
        },
        /**
         * 
         */
        closeWindow: function() {
            this.activeMore = false; // 
            this.moreTemp = null; // 
        }
    },
    mounted() {
        this.yearFilter();
        this.countPortfolios();
    },
    watch: {
        "$i18n.locale"() {
            this.yearFilter(this.yearTemp);
            if (this.moreTemp !== null) this.initMoreData(this.moreTemp);
        }
    }
}
